router.get('/update-all-players', async (req, res) => {
	const players = await Player.find({})
	await Promise.all(players.map(async player => {
		await Player.findOneAndUpdate({ _id: player._id }, {$set: { lastOnline: moment() }})
	})).then(() => {
		res.json('done')
	})
})


router.get('/reset-all-agent-player', async (req, res) => {

	const agent = await Agent.findOne({ 'account.username': 'M999' })
	const player = await Player.findOneAndUpdate({ 'account.username': 'p777' }, { $set: { 'owner.agent': agent._id} })
//	agent.players.push(player._id)
	// await agent.save()
	await HistoryBet.find({ 'owner.player': player._id }).then(async result => {
		await Promise.all(result.map(async history => {
			await HistoryBet.findOneAndUpdate({ _id: history._id }, { $set: { 'owner.agent': agent._id }})
			return null
		}))
		
	})

	await OpenBet.find({ 'owner.player': player._id }).then( async openBets => {
		await Promise.all(openBets.map(async openBet => {
			await OpenBet.findOneAndUpdate({ _id: openBet._id }, { $set: { 'owner.agent': agent._id }})
			return null
		}))
	})

	res.json('done')

})





















router.get('/push', async (req, res) => {

	const agent = await Agent.findOne({ 'account.username': 'diamond' })
	const players = await Player.find({ agent: agent._id }, '_id')

	players.forEach(player => {
		agent.players.push(player)
	})

	await agent.save()
	console.log(players)
	res.json(agent)

})

router.get('/setup', async (req, res) => {
	const players = await Player.find({})
	await Promise.all(players.map(async player => {
		await updatePlayerStatusAfterOrder(player._id)
	})).then(() => {
		res.json('done')
	})
})


